import * as BABYLON from 'babylonjs';

export default class Bat {
    constructor( scene, mesh ) {
        this.scene = scene;
        this.mesh = mesh;

        this.mesh.position.y = 50;
        this.mesh.position.z = 90;

        this.directionLine = new BABYLON.MeshBuilder.CreateLines(
            'direction-line',
            {
                points: [
                    new BABYLON.Vector3(0, 0, 0),
                    new BABYLON.Vector3(600, 0, 0)
                ],
                colors: [
                    new BABYLON.Color4(0, 0, 0.8, 1),
                    new BABYLON.Color4(0, 0, 0.8, 1),
                ]
            }
        )
    }

    getMesh = () =>{ return this.mesh };
    
    update = (position, rotation) => {
        this.mesh.position = position;
        this.mesh.rotation = rotation;
    }

    updateDirectionLine = (position, angle) => {
        this.directionLine.position = position;
        this.directionLine.rotation.y = angle;
    }
}